@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap");
/* CSS RESET */
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* APP */

body {
  background-color: #26003b;
  background-image: url("https://www.transparenttextures.com/patterns/cartographer.png");
  color: white;

  font-family: "Rubik", sans-serif;
}

/* container */

.container {
  max-width: 1200px;
  margin: 0 auto;
}

/* todos */

.title {
  text-align: center;
  font-size: 3rem;
  padding: 3rem 0 1rem;
}

.todos {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.todo {
  display: flex;
  justify-content: space-between;
  padding: 1rem 1.25rem;

  background-color: rgba(255, 255, 255, 0.1);
  width: 50%;
  font-size: 1.5rem;
  border-radius: 1rem;
  transition: all 0.2s;

  &:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
}

.todo__status,
.todo__delete {
  border: none;
  padding: 0;
  line-height: 1;
  cursor: pointer;
  background: none;
  color: white;
  transition: all 0.2s;

  &:hover {
    transform: scale(1.15);
  }
}

.todo__status {
  font-size: 1.75rem;
}

.mutations {
  display: flex;
  gap: 0.75rem;
}

/* form */

.form {
  display: flex;
  gap: 0.5rem;
  width: 50%;
  margin: 1rem auto;
}

.form__input {
  flex: 1;
  padding: 0.5rem 0.75rem;
  border-radius: 0.25rem;
  border: none;
  outline: none;
  background-color: rgba(0, 0, 0, 0.91);

  &:focus,
  &:hover {
    background-color: rgba(255, 255, 255, 0.91);
  }
}

.form__button {
  border: none;
  background-color: rgba(113, 0, 179, 0.5);
  cursor: pointer;
  color: black;
  border-radius: 0.25rem;
  transition: all 0.2s;

  &:hover {
    background-color: rgba(95, 0, 179, 0.9);
    color: white;
  }
}

@media (max-width: 420px) {
  .title {
    font-size: 2.5rem;
  }

  .form {
    width: 95%;
  }

  .todo {
    width: 95%;
    font-size: 1.2rem;
  }
}
